<template>
    <div class="musePage" :id="page.id" v-bind="name" :style="{backgroundColor:page.backcolor, width:page.width + 'px', height: page.height + 'px', visibility: IsVisibility}">
        <muse-label v-for="label in page.label" :key="label" :labelnode="label" />
        <muse-text-field v-for="textfield in page.textfield" :key="textfield" :textfieldnode="textfield" />
        <muse-select-box v-for="selectbox in page.selectbox" :key="selectbox" :selectboxnode="selectbox" />
        <muse-key :actionnode="page.action" @show-page-event="showPage"/>
    </div>
</template>

<style>
    .musePage {
        position: absolute; 
        width:240px; 
        height:320px;
        }
</style>

<script src="node_modules/jquery/dist/jquery.js"></script>
<script>
import MuseLabel from '@/ui/control/MuseLabel.vue'
import MuseTextField from '@/ui/control/MuseTextField.vue'
import MuseSelectBox from '@/ui/control/MuseSelectBox.vue'
import MuseKey from '@/ui/key/MuseKeyBase.vue';
export default {
    name : "MusePage",
    props: [ "pagenode", "showpagename" ],
    components : {
        MuseLabel,
        MuseTextField,
        MuseSelectBox,
        MuseKey,
    },
    data() {
        return {
            page: this.pagenode,
            name: this.pagenode.id,
        }
    },
    methods: {
        showPage(pageName) {
            this.$emit("show-page-event", pageName);
        },
    },
    computed: {
        IsVisibility: function() {
            if(this.showpagename == this.pagenode.id) {
                return "visible";
            }
            return "hidden";
        }
    }
};
</script>
