<template>
  <muse-main-form :style="{width:screen_width + 'px', height: screen_height + 'px'}">
    <!--<muse-page v-for="page in showPages" :key="page" :pagenode="page" :ref="page.id" @show-page-event="changePage"/>-->
    <muse-page v-for="page in kntool.page" :key="page" :pagenode="page" :showpagename="showPage" ref="page" @show-page-event="changePage"/>

    <label style="position: absolute;" :style="{left: button_x + 'px', top: button_y + 'px'}">{{hello}}</label>
    <input type="file" id="file-selector" style="position: absolute;" :style="{left: button_x + 'px', top: (button_y + 40) + 'px'}" accept=".kaml" @change="onLoadKaml"/>

    <button style="position: absolute; width: 100px; height: 50px; background-color: lightcyan;" :style="{left: (button_x + 0) + 'px', top: (button_y + 90) + 'px'}" data-id="00001" @click="loadKaml($event)">入出庫アプリ</button>
    <button style="position: absolute; width: 100px; height: 50px; background-color: lightcyan;" :style="{left: (button_x + 150) + 'px', top: (button_y + 90) + 'px'}" data-id="00002" @click="loadKaml($event)">照合アプリ</button>
    <button style="position: absolute; width: 100px; height: 50px; background-color: lightcyan;" :style="{left: (button_x + 300) + 'px', top: (button_y + 90) + 'px'}" data-id="00003" @click="loadKaml($event)">棚卸アプリ</button>
    <!--<v-btn :style="{left: button_x + 'px', top: (button_y + 100) + 'px'}" data-id="00002" @click="loadKaml()">照合アプリ</v-btn>
    <v-btn :style="{left: button_x + 'px', top: (button_y + 100) + 'px'}" data-id="00003" @click="loadKaml()">棚卸アプリ</v-btn>
    -->
  </muse-main-form>
</template>

<script src="node_modules/jquery/dist/jquery.js"></script>
<script>
import MuseMainForm from '@/ui/MuseMainForm.vue'
import MusePage from '@/ui/MusePage.vue'
export default {
  name: 'App',

  components: {
    MusePage
  },

  created() {
    var $ = require('jquery');
    var xmlHttp = new XMLHttpRequest();

    if(window.innerWidth < window.innerHeight) {
      this.scall = window.innerWidth / this.screen_width;
      this.screen_width = window.innerWidth;
      this.screen_height = this.screen_height * this.scall;
      this.button_x = 0;
      this.button_y = this.screen_height + 10;
    } else {
      this.scall = window.innerHeight / this.screen_height;
      this.screen_height = window.innerHeight;
      this.screen_width = this.screen_width * this.scall;
      this.button_x = this.screen_width + 10;
      this.button_y = 0;
    }

    var me = this;
    xmlHttp.onreadystatechange = function() {
        if(xmlHttp.readyState == 4) {
            if(xmlHttp.status == 200) {
                //var knbase = $("#main");
                var xml = xmlHttp.responseXML.documentElement;
                //me.setKamlLayput(me, docelem);

                me.SerializeRoot(xml);
            }
        }
    }
    //xmlHttp.open("GET", "https://kntool.github.io/app/kntool.xml");
    xmlHttp.open("GET", "https://kntool.github.io/app/app/00001.xml");
    xmlHttp.send();
  },
  mounted() {
  },
  methods: {
    SerializeRoot: function(xml) {
      var layouts = xml.getElementsByTagName("layout");

      if((layouts != null) && (layouts.length > 0)) {
        for(var i = 0; i < layouts.length; i++) {
          var pages = layouts[i].getElementsByTagName("page");
          if((pages != null) && (pages.length > 0)) {
            for(var j = 0; j < pages.length; j++) {
              //for(var j = 0; j < 1; j++) {
              this.SerializePage(pages[j]);
            }
          }
          try {
            this.showPage = layouts[0].getAttribute("topPage");
          } catch {}
        }

        var infomations = xml.getElementsByTagName("infomation");
        if((infomations != null) && (infomations.length > 0)) {
          this.kntool.title = infomations[0].getAttribute("title");
          this.kntool.version = infomations[0].getAttribute("version");
        }

        this.hello = this.kntool.title + " - Ver." + this.kntool.version;
      }
    },
    SerializePage(node) {
      var id = null;
      var backcolor = null;
      var type = 0;

      var museLabels = Array();
      var museTextFields = Array();
      var museSelectBoxs = Array();
      var museAction = null;

      if(node.getAttribute("id") != null) {
        id = node.getAttribute("id");
      }
      if(node.getAttribute("backcolor") != null) {
        backcolor = "#" + node.getAttribute("backcolor").substring(2, 8);
      }
      if(node.getAttribute("type") != null) {
        type = parseInt(node.getAttribute("type"));
      }

      var labels = node.getElementsByTagName("label");
      if((labels != null) && (labels.length > 0)) {
        for(var n = 0; n < labels.length; n++) {
          museLabels.push(this.SerializeLabel(labels[n]));
        }
      }

      var textFields = node.getElementsByTagName("textFiled");
      if((textFields != null) && (textFields.length > 0)) {
        for(var n = 0; n < textFields.length; n++) {
          museTextFields.push(this.SerializeTextField(textFields[n]));
        }
      }

      var selectboxs = node.getElementsByTagName("selectBox");
      if((selectboxs != null) && (selectboxs.length > 0)) {
        for(var n = 0; n < selectboxs.length; n++) {
          museSelectBoxs.push(this.SerializeSelectBox(selectboxs[n]));
        }
      }

      var actions = node.getElementsByTagName("action");
      if((actions != null) && (actions.length > 0)) {
        museAction = this.SerializeAction(actions[0]);
      }

      this.kntool.page.push({
        id: id,
        backcolor: backcolor,
        type: type, 
        width: this.screen_width,
        height: this.screen_height,
        label: museLabels,
        textfield: museTextFields,
        selectbox: museSelectBoxs,
        action: museAction,
      });

    },
    SerializeLabel(node) {
      var id = null;
      var x = 0;
      var y = 0;
      var width = 0;
      var height = 0;
      var system = 0;
      var common = "";
      var text = "";
      var textsize = 12;
      var textcolor = "FFFFFF";
      var valign = "left";
      var halign = "top";
      var textstyle = 0;
      var backcolor = "transparent";
      var bordercolor = "transparent"
      var borderwidth = 0;
      var borderstyle = "";

      try {
        id = node.getAttribute("id");
      } catch {}
      try {
        x = parseInt(node.getAttribute("x")); 
      } catch {}
      try {
        y = parseInt(node.getAttribute("y"));
      } catch {}
      try {
        width = parseInt(node.getAttribute("width"));
      } catch {}
      try {
        height = parseInt(node.getAttribute("height"));
      } catch {}
      try {
        system = parseInt(node.getAttribute("system"));
      } catch {}
      try {
        switch(system) {
          case 0:
            text = node.getAttribute("text");
            break;
          case 5:
            text = "Ver.x.x.x.x";
            break;
        }
      } catch {}
      try {
        textsize = parseInt(node.getAttribute("textsize"));
      } catch {}
      try {
        textcolor = "#" + node.getAttribute("textcolor").substring(2, 8);
      } catch {}
      try {
        backcolor = "#" + node.getAttribute("backcolor").substring(2, 8);
      } catch {}
      try {
        switch(parseInt(node.getAttribute("textalign")))
        {
          case 0x1:
            valign = "left";
            halign = "top";
            break;
          case 0x2:
            valign = "center";
            halign = "top";
            break;
          case 0x4:
            valign = "right";
            halign = "top";
            break;
          case 0x10:
            valign = "left";
            halign = "middle";
            break;
          case 0x20:
            valign = "center";
            halign = "middle";
            break;
          case 0x40:
            valign = "right";
            halign = "middle";
            break;
          case 0x100:
            valign = "left";
            halign = "bottom";
            break;
          case 0x200:
            valign = "center";
            halign = "bottom";
            break;
          case 0x400:
            valign = "right";
            halign = "bottom";
            break;
        }
      } catch {}
      try {
        switch(parseInt(node.getAttribute("borderstyle")))
        {
          case 1:
            borderstyle = "solid";
            break;
        }
      } catch {}
      try {
        borderwidth = parseInt(node.getAttribute("borderwidth"));
      } catch {}
      try {
        bordercolor = "#" + node.getAttribute("bordercolor").substring(2, 8);
      } catch {}

      return {
        id: id,
        x: x * this.scall,
        y: y * this.scall,
        width: width * this.scall,
        height: height * this.scall,
        text: text,
        textsize: textsize * this.scall,
        textcolor: textcolor,
        backcolor: backcolor,
        valign: valign,
        halign: halign,
        borderstyle: borderstyle,
        borderwidth: borderwidth * this.scall,
        bordercolor: bordercolor,
      };
    },
    SerializeTextField(node) {
      var id = null;
      var common = null;
      var label = "";
      var x = 0;
      var y = 0;
      var width = 0;
      var height = 0;
      var activecolor = "black";
      var textsize = 12;

      try {
        id = node.getAttribute("id");
      } catch {}
      try {
        common = node.getAttribute("common");
      } catch {}
      try {
        label = node.getAttribute("label");
      } catch {}
      try {
        x = parseInt(node.getAttribute("x")); 
      } catch {}
      try {
        y = parseInt(node.getAttribute("y"));
      } catch {}
      try {
        width = parseInt(node.getAttribute("width"));
      } catch {}
      try {
        height = parseInt(node.getAttribute("height"));
      } catch {}
      try {
        activecolor = "#" + node.getAttribute("activebackcolor").substring(2, 8);
      } catch {}
      try {
        textsize = parseInt(node.getAttribute("textsize"));
      } catch {}

      return {
        id: id,
        common: common,
        label: label,
        x: x * this.scall,
        y: y * this.scall,
        width: width * this.scall,
        height: height * this.scall,
        activecolor: activecolor,
        textsize: textsize,
      };
    },
    SerializeSelectBox(node) {
      var id = null;
      var label = "";
      var x = 0;
      var y = 0;
      var width = 0;
      var height = 0;
      var activecolor = "black";

      try {
        id = node.getAttribute("id");
      } catch {}
      try {
        label = node.getAttribute("label");
      } catch {}
      try {
        x = parseInt(node.getAttribute("x")); 
      } catch {}
      try {
        y = parseInt(node.getAttribute("y"));
      } catch {}
      try {
        width = parseInt(node.getAttribute("width"));
      } catch {}
      try {
        height = parseInt(node.getAttribute("height"));
      } catch {}
      try {
        activecolor = "#" + node.getAttribute("activebackcolor").substring(2, 8);
      } catch {}

      return {
        id: id,
        label: label,
        x: x * this.scall,
        y: y * this.scall,
        width: width * this.scall,
        height: height * this.scall,
        activecolor: activecolor,
      };
    },

    SerializeAction(node) {
      var museKeys = Array();
      
      var keys = node.getElementsByTagName("key");
      if((keys != null) && (keys.length > 0)) {
        for(var n = 0; n < keys.length; n++) {
          museKeys.push(this.SerializeKey(keys[n]));
        }
      }

      return {
        width: this.screen_width,
        height: 30 * this.scall,
        key: museKeys,
      };
    },

    SerializeKey(node) {
      var vk = 0;
      var action = 0;
      var text = "";
      var textsize = 12;
      var color = "Red";
      var show = false;
      var width = 49;
      var x = 0;

      try {
        vk = parseInt(node.getAttribute("vk"));
      } catch {}
      try {
        action = node.getAttribute("action");
      } catch {}

      switch(vk) {
        case 132:
          color = "#FF2020";
          x = 7;
          break;
        case 112:
          color = "#2010FF";
          x = 66;
          break;
        case 113:
          color = "#10C010";
          x = 125;
          break;
        case 133:
          color = "#FFFF00";
          x = 184;
          break;
      }

      var label = node.getElementsByTagName("keyLabel");
      if((label != null) && (label.length > 0)) {
        try {
          text = label[0].getAttribute("text");
        } catch {}
        try {
          textsize = parseInt(label[0].getAttribute("textsize"));
        } catch {}
        try {
          if(label[0].getAttribute("hide") == "0") {
            show = true;
          }
        }catch {}
      }

      var success = null;
      var fail = null;

      var result = node.getElementsByTagName("result");
      if((result != null) && (result.length > 0)) {
        var s = result[0].getElementsByTagName("success");
        if((s != null) && (s.length > 0)) {
          success = this.SerializeResult(s[0]);
        }
        var f = result[0].getElementsByTagName("fail");
        if((f != null) && (f.length > 0)) {
          fail = this.SerializeResult(f[0]);
        }
      }

      return {
        vk: vk,
        action: action,
        text: text,
        textsize: textsize * this.scall,
        color: color,
        show: show,
        width: width * this.scall,
        height: 22 * this.scall,
        x: x * this.scall,
        y: 4 * this.scall,
        success: success,
        fail: fail,
      };
    },
    SerializeResult(node) {
      var page = null;
      var buzzer = null;
      var error = false;

      try {
        page = node.getAttribute("page");
      } catch {}
      try {
        buzzer = node.getAttribute("buzzer");
      } catch {}
      try {
        error = node.getAttribute("error");
      } catch {}

      return {
        page: page,
        buzzer: buzzer,
        error: error,
      };
    },

    changePage(pageName) {
      this.showPage = pageName;
      this.getCommonText();
    },
    getCommonText() {
      var ref = this.$refs.page;
      for(var n = 0; n < ref.length; n++) {
      console.log(ref[n].name);
      }
    },

    onLoadKaml: function(e) {
        if(window.File && window.FileReader && window.FileList && window.Blob) {
            var fileData = e.target.files[0];
            var reader = new FileReader();
            var me = this;

            reader.onload = function() {
                var doc = reader.result;

                var parser = new DOMParser();
                var xml = parser.parseFromString(doc, "application/xml");

                me.SerializeRoot(xml);
            };
            reader.readAsText(fileData, 'UTF-8');
        }
    },
    loadKaml: function(event) {
      var me = this;
      var url = "https://kntool.github.io/app/app/" + event.target.dataset.id + ".xml"

      var xmlHttp = new XMLHttpRequest();

      xmlHttp.onreadystatechange = function() {
          if(xmlHttp.readyState == 4) {
              if(xmlHttp.status == 200) {
                  var xml = xmlHttp.responseXML.documentElement;

                  me.SerializeRoot(xml);
              }
          }
      }
      xmlHttp.open("GET", url);
      xmlHttp.send();
      },
  },
  computed :{
    showPages: function() {
      var pages = new Array();
      if((this.app != null) && (this.kntool.page !=null) && (this.kntool.page.length > 0)) {
        for(var n = 0; n < this.kntool.page.length; n++) {
          if(this.kntool.page[n].id == this.showPage) {
            pages.push(this.kntool.page[n]);
          }
        }
      }

      return pages;
    },
  },
  data: () => ({
    //
    hello: "",
    showPage: "",
    kntool: {
      title: "",
      version: "",
      showPage: "",
      page: [],
    },

    screen_width: 240,
    screen_height: 297,
    scall: 1.0,

    button_x: 0,
    button_y: 0,
  }),
};
</script>
