<template>
    <button class="museKeyStyle" :style="{
        left: keynode.x + 'px', 
        top: keynode.y + 'px',
        width: keynode.width + 'px', 
        height: keynode.height + 'px',
        backgroundColor: keynode.color, 
        fontSize: keynode.textsize + 'px'
        }"
        @click="onclick" 
        v-bind="pageName"
        >
    {{keynode.text}}
    </button>
</template>

<style>
    .museKeyStyle {
        position: absolute;
        top: 4px;
        height: 22px;
        text-align: center;
        vertical-align: middle;
    }
</style>

<script src="node_modules/jquery/dist/jquery.js"></script>
<script>
export default {
    name: "MuseKey",
    props: ["keynode"],
    methods: {
        onclick: function() {
            var $ = require('jquery');

            if((this.keynode.success != null) && (this.keynode.success.page != null)) {
                this.$emit("show-page-event", this.keynode.success.page);
            }
        },
    },
    data() {
        return {
        }
    },
}; 
</script>