<template>
    <div 
        class="museTextFeild" :id="textfieldnode.id" :ref="textfieldnode.common"
        :style="{
            left: textfieldnode.x + 'px',
            top: textfieldnode.y + 'px',
            width: textfieldnode.width + 'px',
            height: textfieldnode.height + 'px',
            fontSizeRoot: textfieldnode.textsize + 'px',
            }"
    >
    <v-text-field        
        :label="textfieldnode.label"
        :style="{
            color: textfieldnode.activecolor,
            }"
        v-model="text"
    />
    </div>
</template>

<style>
    .museTextFeild {
        position: absolute;
        }
</style>

<script>
export default {
    name : "MuseTextField",
    props: ["textfieldnode"],
    data() {
        return {
            text: "",
        }
    },
};
