<template>
    <div class="museKeyBase" :style="{width:action.width + 'px', height:action.height + 'px'}">
        <muse-key v-for="mkey in showKeys" :key="mkey" :keynode="mkey" @show-page-event="showPage"/>
    </div>
</template>

<style>
    .museKeyBase {
        position: absolute;
        left: 0px;
        bottom: 0px;
        width: 240px;
        height: 30px;
        background-color: black;
    }
</style>

<script src="node_modules/jquery/dist/jquery.js"></script>
<script>
import MuseKey from './MuseKey.vue';
export default {
    name: "MuseKeyBase",
    props: ["actionnode"],
    components: {
        MuseKey,
    },
    data() {
        return {
            action: this.actionnode,
        }
    },
    methods: {
        showPage(pageName) {
            this.$emit("show-page-event", pageName);
        },
    },
    computed: {
        showKeys: function() {
            var keys = new Array();
            if((this.actionnode != null) && (this.actionnode.key != null) && (this.actionnode.key.length > 0)) {
                for(var n = 0; n < this.actionnode.key.length; n++) {
                    if(this.actionnode.key[n].show) {
                        keys.push(this.actionnode.key[n]);
                    }
                }
            }

            return keys;
        },
    },
};
</script>
